.container {
  overflow: hidden;
  border-radius: 1rem;
}

.title {
  margin-top: 0;
  padding: 1rem;
  text-align: center;
  background-color: dodgerblue;
  color: #444;
}

.grid {
  max-width: 1100px;
  margin: 5%;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(300px, auto));
  justify-items: center;
}

.card {
  min-height: 400px;
  height: min;
  min-width: 300px;
  width: 100%;
  box-shadow: 0px 3px 10px #fff1;
  border-radius: 0.5rem;
  overflow: hidden;
  cursor: pointer;
  scroll-snap-align: start;
}

.image {
  height: 200px;
  width: 100%;
  background-position: 30% 30%;
  background-size: cover;
  background-repeat: no-repeat;
}

.card h2 {
  margin: 0.5rem 0.5rem;
  text-align: left;
  color: goldenrod;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  margin: 0.1rem;
}

.tag {
  margin: 0.2rem 0.2rem;
  padding: 0.2rem 0.5rem;
  background-color: skyblue;
  color: #333;
  border-radius: 0.4rem;
}

.description {
  margin: 1rem;
}
