.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 1100px;
  margin: 5%;
  overflow:hidden;
}
.table {
  border: 1px solid red;
  margin: 1rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  width: 98%;
}

.row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  border: 1px solid yellow;
  padding: 1rem;
  width: 98%;
}
.row > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.2rem;
  border: 1px solid maroon;
}

