.themeContainer {
    margin: 1rem;
    background-color: #777;
    height: 15px;
    width: 45px;
    border-radius: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    transition: 0.3s;
}

.slider {
    height: 15px;
    width: 15px;
    border-radius: 50%;
    transition: margin 700ms;
}

.themetext {
    font-size: 8px;
}

