.main {
  max-width: 1100px;
  margin: 5%;
  z-index: 5;
}

.title {
  width: 100%;
  margin-top: 0;
  padding: 1rem;
  text-align: center;
  background-color: dodgerblue;
}

.tech {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(auto-fill, minmax(80px, auto)) ;
}

.card{
  border: 1px solid white;
  position: relative;
  width: 100%;
  display: flex;
  border: 1px solid #55555599;
  border-radius: 1rem;
  background-color: dodgerblue;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.15);
  z-index: 1;
}

.card:nth-child(1) {
  background-color: #f7df1e;
}

.card:nth-child(3) {
  background-color: #007acc;
}

.card:is(:hover, :active, :focus) .toolname {
  left: 0;
}

img {
  width: 100%;
}

.toolname {
  padding: 0;
  margin: auto;
  transition: 0.2s;
  color: goldenrod;
  font-size: 1.0rem;
  text-shadow: 0px 0px 3px white,0px 0px 5px white,0px 0px 8px white,0px 0px 11px white;
  font-weight: bold;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  position: absolute;
  /* top: 0; */
  left: -110%;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

@media (max-width: 980px) {
  .main {
    margin: 2rem;
  }

  .tech {
    grid-gap: 1.5rem;
  }

  .card {
    border-radius: 0.8rem;
  }

  .toolname {
  }
  .toolname:nth-child(1),
  .toolname:nth-child(2) {
  }
}

@media (max-width: 768px) {
  .main {
    margin: 2rem;
  }

  .tech {
    grid-gap: 1.5rem;
  }

  .card {
    border-radius: 0.8rem;
  }

  .toolname {
  }
}

@media (max-width: 500px) {
  .main {
    margin: 1rem;
  }
  .tech {
    grid-gap: 0.8rem;
  }
  .card {
    border-radius: 0.5rem;
  }
  .toolname {
  }
}
